
body{
  margin: 0;
  padding: 0;
  box-sizing: border-box
}

.main-cont {
    position: relative; 
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
    grid-template-rows: repeat(auto-fill, minmax(350px, 1fr));
    grid-gap: 20px;
  }

  .main-section{
    position: relative;
    padding: 6%;
    background:  linear-gradient(90deg, rgba(229,235,242,1) 0%, rgba(255,255,255,1) 24%, rgba(250,250,250,1) 74%, rgba(229,235,242,1) 100%);
  box-shadow: 3px 3px 10px black ;
  border-radius: 5px;
}

  .logo-container {
      width: 40%;
      max-height: 50%;
      margin-left: 27%;
  }

  .logo-container > img {
    height: 100%;
  }

  .tap {
     position: absolute;
     left: 80%;
    width: 50px;
    height: 50px;
    background-color: orange;
    border-radius: 50%;
    text-align: center;
    padding-top: .2em
  }
  .tap p {
  font-size: .6em;
  color: white;
  font-weight: 700;
  
}

.tap p:last-child {
  margin-top: -.30em;
  font-size: 1.7em
}

.app-title {
    font-family: 'Teko', cursive;
    text-shadow: 3px 3px 7px grey;
    margin-left: 32%;
    font-size: 450%;
    color: red;
    font-weight: 600;
    margin-top: 3%;
    margin-bottom: 2%;
    text-shadow: 1px 1px 4px black;
    text-transform: uppercase
}

.details {
  text-align: left;
}

  .main-details h1 {
      color: black;
      font-size: 2em;
      margin-top: 10px;
      line-height: 85%;
      margin-left: auto
  }

  .main-details h3 {
      margin-bottom: 6%;
      font-size: 1.3em;
  }

  .main-details p {
      line-height: 85%;
      font-size: 1.1em;
      margin-top: -15px;
  }

  .main-bottom {
      position: relative;
      display: flex;
      top: 2px;
      margin-top: 5px;
    }

  .main-bottom p {
      margin: 8px 0 0 0;
      line-height: 99%;
      font-size: 1em;
      border: 1px solid black;
      padding: 2px 12px;

  }

  .main-description {
    margin-top: 6%;
  }

  .main-description-small {
    height: 3em;
    font-size: 1em;
  }

  .main-description p {
    font-size: 1em;
    overflow: hidden;
  }

  .btn-readmore {
    margin-top: .5em;
    padding: 1% 2%;
  }

  .untappd-btn {
    position: absolute;
    top: 88%;
    left: 60%;
    font-size: .7em
  }

  .dashboard {
    margin-left: 90%
  }

  .spinner {
    width:150px;
    margin-left:140%;
    margin-top:20%;
    display: block
  }

  @media screen and (max-width: 1150px){
    .app-title {
      margin-left: 25%;
    }
  }

  @media screen and (max-width: 820px){
    .app-title {
      margin-left: 15%;
    }
    .dashboard {
      margin-left: 75%;
      font-size: .9em;
    }
    .spinner {
      margin-left: 90%
    }
  }

  @media screen and (max-width: 630px) {
    .app-title {
      margin-left: 8%;
      font-size: 400%;
      margin-top: 4%;
    margin-bottom: 4%;
    }
    
  }

  @media (max-width: 525px) {
    .app-title {
      font-size: 300%;
      margin-left: 4%;
    }
    .tap {
   
      width: 50px;
      height: 50px;}
      .spinner {
        width: 100px;
        margin-left: 15%;
        margin-top: 10%
      }
  }

  @media (max-width: 400px) {
    .app-title {
      font-size: 260%;
      margin-left: 3%;
    }
    .main-section {
      width: 50%
    }
  }
