*,*:after,*:before {
    box-sizing:border-box;
    -moz-box-sizing:border-box;
    -webkit-box-sizing:border-box;
  }
  
  .cf:before,
  .cf:after {
      content:"";
      display:table;
  }
  .cf:after {
      clear:both;
  }
  
  
  a,a:visited {
    color:#fff
  }

  .searchform {
    background:#f4f4f4;
    background:rgba(244,244,244,.79);
    border: 1px solid #d3d3d3;
    padding: 2px 5px;
    padding-left: 10%;
    width:100%;
    box-shadow:0 4px 9px rgba(0,0,0,.37);
    -moz-box-shadow:0 4px 9px rgba(0,0,0,.37);
    -webkit-box-shadow:0 4px 9px rgba(0,0,0,.37);
    border-radius: 10px;
    -moz-border-radius: 10px;
    -webkit-border-radius: 10px;
    display: flex;
    margin-bottom: 3%;
  }
  
  .searchform input, .searchform button {
      float: left
  }
  .searchform input {
      background:#fefefe;
      border: none;
      font: 18px 'HelveticaNeue', Helvetica, Arial, sans-serif;
      margin-right: 10%;
      padding: 10px;
      width: 40%;
      box-shadow: 0 0 4px rgba(0,0,0,.4) inset, 1px 1px 1px rgba(255,255,255,.75);
      -moz-box-shadow: 0 0 4px rgba(0,0,0,.4) inset, 1px 1px 1px rgba(255,255,255,.75);
      -webkit-box-shadow: 0 0 4px rgba(0,0,0,.4) inset, 1px 1px 1px rgba(255,255,255,.75);
    border-radius: 9px;
    -moz-border-radius: 9px;
    -webkit-border-radius: 9px;
  }
      .searchform input:focus {
          outline: none;
          box-shadow:0 0 4px #0d76be inset;
          -moz-box-shadow:0 0 4px #0d76be inset;
          -webkit-box-shadow:0 0 4px #0d76be inset;
      }
      .searchform input::-webkit-input-placeholder {
        font-style: italic;
        line-height: 15px
      }
  
      .searchform input:-moz-placeholder {
        font-style: italic;
        line-height: 15px
      }
  
  .searchform .btn {
      background: rgb(52,173,236);
      background: -moz-linear-gradient(top, rgba(52,173,236,1) 0%, rgba(38,145,220,1) 100%);
      background: -webkit-linear-gradient(top, rgba(52,173,236,1) 0%,rgba(38,145,220,1) 100%);
      background: -o-linear-gradient(top, rgba(52,173,236,1) 0%,rgba(38,145,220,1) 100%);
      background: -ms-linear-gradient(top, rgba(52,173,236,1) 0%,rgba(38,145,220,1) 100%);
      background: linear-gradient(to bottom, rgba(52,173,236,1) 0%,rgba(38,145,220,1) 100%);
      filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#34adec', endColorstr='#2691dc',GradientType=0 );
      border: none;
      color:#fff;
      cursor: pointer;
      font: 13px/13px 'HelveticaNeue', Helvetica, Arial, sans-serif;
      padding: 10px;
      width: 35%;
      box-shadow: 0 0 2px #2692dd inset;
      -moz-box-shadow: 0 0 2px #2692dd inset;
      -webkit-box-shadow: 0 0 2px #2692dd inset;
    border-radius: 9px;
    -moz-border-radius: 9px;
    -webkit-border-radius: 9px;
  }
      .searchform .btn:hover {
          opacity:.9;
      }

      @media (max-width: 600px) {
        .searchform {
          padding-left: 2%;
        }
         .searchform input {
           width: 50%;
           font-size: 1em;
         }
         .searchform .btn {
           width: 50%;
         }
      }

      @media (max-width: 500px) {
        .searchform {
          padding-left: 1%;
        }
         .searchform input {
           width: 150%;
           font-size: 1em;
           margin: 0 1% 0 0;
         }
         .searchform .btn {
           width: 150%;
           margin: 0;
         }
      }