
.title {
    background: linear-gradient(60deg, rgba(84,58,183,1) 0%, rgba(0,172,193,1) 100%);
    margin-bottom: .1em;
    margin-top: 5%;
    display: flex;
    padding: 2% .5em 0; 
    justify-content: space-between;
    color: white;
    justify-content: center
}

.dashboard-title{
    font-family: 'Teko', cursive;
    margin-left: 39%;
    font-size: 450%;
    text-shadow: 1px 1px 4px black;
    text-transform: uppercase
}

.logout {
    height: 8%;
    margin-left: auto;
    margin-top: 1.5%;
}

@media (max-width:1150px) {
    .dashboard-title {
        font-size: 350%;
        margin-left: 38%;
    }
}

@media (max-width: 900px){
    .dashboard-title {
        margin-left: 30%;
    }
}

@media screen and (max-width: 600px) {
    .dashboard-title {
        margin-left: 20%;
        font-size: 300%;
    }
}

@media (max-width: 500px) {
    .dashboard-title {
        margin-left: 5%;}
}

@media (max-width: 350px) {
    .dashboard-title {
        margin-left: 1%;
        font-size: 250%;
    }
}