.align {
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;

  }

  
  .grid {
    margin-top: 5%;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 5%;
    max-width: 24em;
    width: 90%;
    background-color: #fff;
    border: 1px solid black;
    
  }
   
  a {
    color: inherit;
    text-decoration: none;
    -webkit-transition: color 0.3s;
    transition: color 0.3s;
  }
  
  a:hover {
    color: #1dabb8;
  }
  
  fieldset {
    border: none;
    margin: 0;
  }
  
  input {
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    border: none;
    font: inherit;
    margin: 0;
    outline: none;
    padding: 0;
  }
  
  input[type='submit'] {
    cursor: pointer;
  }
  
  .login__header {
    background-color: #b7b7c4;
    border-top-left-radius: 0.25em;
    border-top-right-radius: 0.25em;
    color:#fff;
    padding: 1.5em;
    text-align: center;
    text-transform: uppercase;
  }
  
  .login__title {
    font-size: 16px;
    font-size: 1rem;
    margin: 0;
  }
  
  .login__body {
    background-color:  #b7b7c4;
    padding: 1.5em;
    position: relative;
    border-top: 5px solid white;

  }
  
  .login__body::before {
    background-color: #fff;
    content: '';
    height: 0.5em;
    left: 50%;
    margin-left: -0.25em;
    margin-top: -0.25em;
    position: absolute;
    top: 0;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    width: 0.5em;
  }
  
  .login input[type='text'],
  .login input[type='email'],
  .login input[type='password'] {
    width: 100%;
    height: 2.5em;
    border: 0.0625em solid #e5e5e5;
    padding: 1em 1.25em;
    border-top-left-radius: 0.25em;
    border-top-right-radius: 0.25em;
  }
  
  .login input[type='submit'] {
    background-color: #1dabb8;
    border-radius: 0.25em;
    color: #fff;
    font-weight: 700;
    -webkit-box-ordinal-group: 2;
        -ms-flex-order: 1;
            order: 1;
    padding: 0.75em 1.25em;
    -webkit-transition: background-color 0.3s;
    transition: background-color 0.3s;
    margin-top: 1.5em;
  }
  
  .login input[type='submit']:focus,
  .login input[type='submit']:hover {
    background-color: #198d98;
  }
  
  .login__footer {
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    background-color: #b7b7c4;
    border-bottom-left-radius: 0.25em;
    border-bottom-right-radius: 0.25em;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    padding-bottom: 1.5em;
    padding-left: 1.5em;
    padding-right: 1.5em;
  }
  
  .login__footer p {
    margin: 0;
  }

  .alert {
    text-align: center;
    color: red;
    text-transform: uppercase;
    line-height: 105%;
    padding: 0;
    margin: 1%;
  }

  

  
  @media screen and (max-width: 600px) {
    .grid {
      width: 70%;
      margin-top:16%;
      margin-bottom: 16%;
    }

  }

  @media screen and (max-width: 350px) {
    .grid {
      margin-top: 20%;
      margin-bottom: 20%;
    }
  }